.rotate {
    font-weight: bold;
    background-color: #343a40;
    text-align: center;
    border-radius: 50%;

}

.container {
    align-items: center;
    width: 100%;
    // background-color: #E7EEF3;
    display: flex;
    justify-content: space-between;
}

.header {
    font-size: 30px;
    text-align: center;
    color: #141313;
}


.li {
    list-style-type: none;

}

.footer {
    margin: 40px 65px;
    width: 100%;
    max-width: 1100px;
    height: 300px;

}

.container__nav {
    font-size: 22px;
    font-weight: 600;
    text-decoration: none;
    list-style-type: none;
    line-height: 30px;
}

.footer {
    display: flex;
    justify-content: space-between;
    margin-left: 110px;
}

.list {

    font-size: 18px;
    line-height: 10px;
    text-decoration: none;
}

.nav__link:hover {
    color: #254c7c;

}

.nav__link {
    text-decoration: none;
    color: black;
    padding-left: 10px;
}

.li {
    list-style-type: none;
    padding-top: 10px;
}

.icon {
    font-size: 2rem;
}