.contacts {
  background-color: #343a40;
  padding: 1.5em;
  text-align: center;
  color: #fff;
  display: flex;
  align-items: center;


  &__inner {
    width: 100%;



  }

  label {
    margin-bottom: 1em;
    display: inline;

  }

  input,
  button {
    margin-bottom: 1em;
    margin-top: 10px;
    border: none;
    padding: 10px 15px;
    width: 50%;
    border-radius: 4px;
  }

  button {
    cursor: pointer;
    background-color: #fff;
    transition: all .2s ease;

    &:hover {
      background-color: rgb(43, 107, 150);
      ;
      color: white;
    }
  }

  p span {
    background-color: #fff;
    color: red;
  }

  body {
    background-color: rgb(155, 103, 82);
  }
}